import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'

class Activities extends Component {

    handleClick = (route)=>{
        window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth'
        });
        this.props.history.push(`/${route}`)
    }

    render(){
        return (
            <div id="activty_section">
                <div className="container center">
                    <h2>What do you want to do ?</h2>
                    <div id="search-icons">
                        <div data-route="flight" className="search-icon" onClick={()=> this.handleClick("flights")}>
                            <img src="/images/service1.png" alt="flights" />
                            <h5>Flights</h5>
                        </div>
                        <div data-route="tour" className="search-icon" onClick={()=> this.handleClick("tours")}>
                            <img src="/images/service2.png" alt="Tour" />
                            <h5>Tour</h5>
                        </div>
                        <div data-route="hotels" className="search-icon" onClick={()=> this.handleClick("hotels")}>
                            <img src="/images/service3.png" alt="Hotels" />
                            <h5>Hotels</h5>
                        </div>
                        <div data-route="events" className="search-icon" onClick={()=> this.handleClick("events")}>
                            <img src="/images/service4.png" alt="Events" />
                            <h5>Events</h5>
                        </div>
                        <div data-route="bus" className="search-icon" onClick={()=> this.handleClick("bus")}>
                            <img src="/images/service5.png" alt="Bus" />
                            <h5>Bus</h5>
                        </div>
                        <div data-route="guide" className="search-icon" onClick={()=> this.handleClick("guide")}>
                            <img src="/images/service6.png" alt="Guide" />
                            <h5>Guide</h5>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(Activities);
