import React from 'react'
import PackageCard from './PackageCard'

function Packages() {
    return (
        <div id="pacakges-container">
            <div className="container" id="pacakges">
                <div id="data">
                    <div>
                        <h2>Exciting Hot Deals on Holiday Packages</h2>
                        <p>All inclusive packages with no hidden costs, including transport, meals, hotel accommodation and all major sightseeings.</p>
                    </div>
                    <a href="#">View all</a>
                </div>        
                <div className="cards">
                    <PackageCard image="/images/Nepal.jpeg" title="Nepal Package" price="₹14,999" />
                    <PackageCard image="/images/Himanchal.jpeg" title="Himanchal Package" price="₹7,999" />
                    <PackageCard image="/images/Rajasthan.jpeg" title="Rajasthan Package" price="₹8,999" />

                    <PackageCard image="/images/Piligrim.jpeg" title="Uttrakhand Package" price="₹5,999" />
                    <PackageCard image="/images/Gujarat.jpeg" title="Gujarat Package" price="₹12,999" />
                    <PackageCard image="/images/Rajasthan.jpeg" title="Rajasthan Package" price="₹8,999" />

                </div>  
                <a href="" >View More</a>  
            </div>
        </div>
    )
}

export default Packages
