import React, { Component } from 'react'
import { Modal } from 'materialize-css/dist/js/materialize.min.js'

class Subscribe extends Component {

    state = {
        email: '',
        instance: null
    }

    componentDidMount(){
        const susbcribeModel = document.querySelector("#email_model");
        this.setState({
            instance: Modal.init(susbcribeModel,{})
        })
    }

    handleChange = (e)=>{
        this.setState({
            email: e.target.value
        })
    }

    handleSubmit = (e)=>{
        this.state.instance.open();
    }

    render(){
        return (
            <div id="subscribe">
                <div id="data">
                    <h1>Subscribe To our newsletter</h1>
                    <h4>Subscribe newsletter to get offers and 
                        <br /> new places to discover.
                    </h4>
                </div>
                <input type="email" id="email" placeholder="Enter Email" onChange={this.handleChange} />
                <button data-target="email_model" className="btn waves-effect waves-light blue" onClick={this.handleSubmit} >
                    Submit
                    <i className="material-icons right">send</i>
                </button>       
                <div id="email_model" className="modal center">
                    <div className="modal-content">
                        <img src="/images/mail.png" alt="Mail Icon" />
                        <h4 className="grey-text">Thanks <strong className="black-text">{ this.state.email }</strong> For Subscribing You Will be Notified About Exicting Offers</h4>
                    </div>
                </div>
            </div>
        )
    }

}

export default Subscribe
