import React from 'react'

function BlogsView() {
    return (
        <section id="blogs">
            <div id="routes">
                <h1>Latest From Our Blogs</h1>
                <a href="#">View all</a>
            </div>
            <div className="blogs">
                <div className="blog">
                    <img src="/images/blogs.jpg" alt="blog1" />
                    <p>13 Oct 2019</p>
                    <h2>Best Moment With Family</h2>
                </div>
                <div className="blog">
                    <img src="/images/blog2.jpg"alt="blog2" />
                    <p>1 Dec 2019</p>
                    <h2>Best Moment With Friends</h2>
                </div>
                <div className="blog">
                    <img src="/images/blog3.jpg" alt="blog3" />
                    <p>2 Jan 2020</p>
                    <h2>Best Trip Ever!!</h2>
                </div>
            </div>
            <a href="#"  className="btn btn-large blue darken-3">view more</a>
        </section>
    )
}

export default BlogsView
