import React, { Suspense, lazy } from 'react';
import Navbar from './components/layout/Navbar'
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import LayoutWrapper from './components/layout/LayoutWrapper'


const DefaultCarousel = lazy(()=> import('./components/layout/DefaultCarousel'))
const FlightCaro = lazy(()=> import('./components/services/FlightCaro'))
const TourCaro = lazy(()=> import('./components/services/TourCaro'))

let loading = (
  <div className="center">
    Loading ...
  </div>
)

function App() {
  return (
    <BrowserRouter>
      <div className="App">
        <Navbar />
        <Suspense fallback={loading}>
          <Switch>
            <Route exact path="/" component={DefaultCarousel} />
            <Route path="/flights" component={FlightCaro} />
            <Route path="/tours" component={TourCaro} />
          </Switch>
        </Suspense>
        <LayoutWrapper />
        <div style={{width: '100%',height: '100vh',background: 'black'}}>

        </div>
      </div>
    </BrowserRouter>
  );
}

export default App;
